import { CSSProperties } from "react";

import { PopoverContentProps, PopoverProps } from "@radix-ui/react-popover";

import { Margins } from "../styleguide";

export type SidePosition = "top" | "bottom";
export type AlignPosition = "start" | "center" | "end";

export interface StyledPopoverProps extends Margins {
	side?: SidePosition;
	align?: AlignPosition;
	$menuWidth?: string;
	$menuHeight?: string;
	children?: React.ReactNode | JSX.Element[];
	radixPopoverContentProps?: Omit<PopoverContentProps, "side" | "align">
	/**
	 * @default ```auto``` set by Radix
	 */
	$zIndex?: CSSProperties["zIndex"];
}

export interface PopoverMenuProps extends StyledPopoverProps {
	dataQA: string;
	ariaLabel: string;
	className?: string;
	trigger?: React.ReactNode;
	radixPopoverProps?: PopoverProps;
	triggerOnClick?: React.MouseEventHandler<HTMLElement>;
	triggerOnFocus?: React.FocusEventHandler<HTMLElement>;
	isTriggerDisabled?: boolean;
}

